.content {
  margin: 30px;
}

.eye {
  margin-left: 5px;
  cursor: pointer;
}

.error {
  color: red;
}

.checkbox {
  cursor: pointer;
}

.captchaBlock {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.captcha {
  margin: 10px 0;
}

.explanation {
  display: flex;
  flex-direction: column;
  margin: 20px 0;
  opacity: 0.7;
}

.link {
  margin: 0 5px;
}

@media screen and (max-width: 740px) {
  .content {
    display: flex;
    justify-content: center;
  }
}
